

import doctorOrOrg from '../../components/DoctorOrOrg';
import moduleTitle from '../../components/ModuleTitle';
import tools from '../../lib/tools.js';
import Toast from 'vux/src/components/toast/index.vue'

import BScroll from 'better-scroll'
export default {
name:'doctorList',
components:{doctorOrOrg,moduleTitle,Toast},
data(){
return {
errorImg:'this.src="' + require('../../assets/morenshangpin3x.png') + '"' ,
id:'',
showToast:false,
toastMsg:'',
tempService:[],
pageNumber:1,
pageSum:0,
container:null,
dragging: false,
times: 0,
loading: false,
loadable:false,
pullUpObj:{
type:0,
text:'松开立即加载'
},// 0 初始化 1可以加载 2最后一页

}
},
mounted(){
this.init();
},
methods:{
init(){
var vm = this;
console.log('init');
this.$http.axios({method:'POST',url:'/jyDoctorInfo/getJySignDoctorInfo',data:{showCount:10,currentPage:this.pageNumber,signOrgId:tools.getRequestParam('orgId'),validTime:tools.simpleDateFormat(new Date(),'yyyy-MM-dd')}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){





this.pageSum = data.totalItemsCount;
this.loading = false;
this.loadable = (this.pageSum>1);
if(this.pullUpObj.type==0){
vm.tempService = data.items;
vm.pullUpObj.type =1;
}else{
vm.tempService = [...vm.tempService,...data.items];
}
}else{
this.loading = false;
this.loadable = false;
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
this.loading = false;
this.loadable = false;
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
signUp(id){
tools.next('/m/familySignUpCreate.html?doctorId='+id);
},
nextPage(){
if(!(this.pageSum==this.times)){
this.times += 1;
this.pageNumber += 1;
console.log('nextPage');
this.init();
}else{
this.pullUpObj.type = 2;
this.pullUpObj.text = '已经到底了'
}
},
reLoad(){
this.pullUpObj = {
type:0,
text:'松开立即加载'
};
this.times = 0;
this.pageNumber = 1;
this.init();
},
goToDoctorIndex(id){
tools.next('/m/doctorIndex.html?md='+id);
}
}
}



